.button {
  padding: 15px 64px;
  font-size: 1em;
  min-width: 250px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 13px;
}
.button:hover,
.button:focus {
  outline: none;
}
.button-primary {
  border: 4px solid #447044;
  background-color: #7D9E7D;
  color: white;
}
.button-primary:hover,
.button-primary:focus {
  background-color: #447044;
  color: white;
  outline: none;
}
.button-secondary {
  border: 4px solid #447044;
  background: none;
  color: #447044;
}
.button-secondary:hover,
.button-secondary:focus {
  background-color: #447044;
  color: white;
  outline: none;
}



